@import '../../sass/colors';

.category {
  padding: 1rem;
  width: 100%;

  @media screen and (min-width: 450px) {
    width: 50%;
  }

  @media screen and (min-width: 992px) {
    width: 25%;
  }
}

.category__container {
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: $white;
  padding: 1rem;
  align-items: center;
}

.category__link {
  margin: auto;
}

.category__img {
  max-width: 100%;
  max-height: 9rem;
}

.category__name {
  font-size: 1rem;
}