.featuredProduct1 {
    height: 32rem;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-color: #555;
    color: #fff;
  
    .container {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
    background-position: center;
      align-items: flex-start;
    }
  }
  
  .banner__logo {
    width: 3rem;
    margin-bottom: .3rem;
    
  }
  
  .featuredProduct1__name {
    text-transform: uppercase;
    font-size: 2rem;
    color: inherit;
    text-shadow: -2px 1px 3px #000;
    text-align: center;

  }
  
  .featuredProduct1__description {
    max-width: 30rem;
    font-size: 1.03rem;
    text-shadow: -2px 1px 3px #000;
    text-align: center;
  }