@import '../../sass/colors';

.footer {
  padding-top: 2rem;
  position: relative;
}

.footer__nav {
  display: flex;
  flex-direction: column;
}

.footer__list {
  padding-left: 0;
  display: flex;
  flex-wrap: wrap;
}

.footer__col {
  width: 100%;
  margin-bottom: 1.5rem;

  @media screen and (min-width: 400px) {
    width: 50%;
  }

  @media screen and (min-width: 768px) {
    width: 33.3333%;
  }

  @media screen and (min-width: 992px) {
    width: 20%;
  }
}

.footerCol__title{

  font-weight: bold;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-size: 1rem;

}



.footerCol__list {
  padding-left: 0;
}

.footerCol__link {
  color: $black !important;

  &:hover {
    color: $primary-color !important;
  }
}

.footer__reLogo {
  align-self: flex-end;
  right: 0;
  width: 5rem;
}

.footer__copyRightRibbon {
  padding: 1rem 0;
  background-color:wheat;
  color: #fff;
  text-align: center;

  .container {
    @media screen and (min-width: 576px) {
      display: flex;
      justify-content: space-between;
    }
  }
}

.footer__cr {
  @media screen and (min-width: 576px) {
    margin-bottom: 0;
    font-weight: bolder;
    font-size: x-large;
  color: black;

  }
}

.footer__powered {
  margin-bottom: 0;
  font-weight: bolder;
  color: black;
  font-size: x-large;
}

.footer__nasorgLogo {
  width: 7rem;
}