@import '../../sass/colors';

.contact {
  padding: 1.8rem 0;

  .container {
    @media screen and (min-width: 768px) {
      display: flex;
      justify-content: center;
      align-items: center;
    }
    background-color: rgb(202, 33, 33);
    color: white;
  }
}


.contact__title {
  text-transform: uppercase;
  font-size: 0.3rem;
  font-weight: bold;
  max-width: 30rem;
  margin-bottom: 0.5rem;
  font-style: oblique;
  

  @media screen and (min-width: 768px) {
    font-size: 1.7rem;
    margin-bottom: 0;
    margin-right: 5rem;
  }
}

.contact__form {
  display: flex;
}

.contact__input {
  border: none;
  padding-left: 1rem;
  transition: box-shadow .15s ease-in-out;
  border-radius: .1rem 0 0 .1rem;

  &:focus {
    outline: none;
    box-shadow: 0 0 0 0.2rem rgba(0,123,255,.25);
  }
}

.contact__btn {
  border: none;
  width: 3rem;
  height: 3rem;
  border-radius: 0 .1rem .1rem 0;
  background-color: $black;
  color: #fff;

  &:focus {
    outline: none;
    box-shadow: 0 0 0 0.2rem rgba(0,123,255,.25);
  }
}